a {
  outline: none;
  text-decoration: none;
}

button {
  cursor: pointer;
  border: none;
  outline: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

input {
  outline: none;
}

body {
  margin: 0;
}

.container {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 100px;
}

html {
  scroll-behavior: smooth;
}

body {
  background-color: #e8e8e2;
  font-family: Neue Montreal;
  font-weight: 500;
}

.is-hidden {
  opacity: 0;
  visibility: 0;
  pointer-events: none;
}

::selection {
  color: #fafafa;
  background-color: #6d8434;
}

.filters {
  padding-bottom: 40px;
}

@media screen and (width >= 1304px) {
  .filters {
    padding-bottom: 60px;
  }
}

.filters__title {
  color: #0101014d;
  margin-bottom: 8px;
  font-family: Neue Montreal;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.29;
}

.filters__body {
  flex-direction: column;
  gap: 12px;
  display: flex;
}

.filters__form {
  width: 100%;
  position: relative;
}

.filters__input {
  background-color: #0000;
  border: 1px solid #0101014d;
  border-radius: 30px;
  width: 100%;
  padding: 14px 20px;
}

.filters__input::placeholder {
  color: #010101;
  font-family: Neue Montreal;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.29;
}

.filters__sub {
  background-color: #0000;
  padding: 0;
  line-height: 0;
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translate(0, -50%);
}

.filters__search {
  fill: #010101;
}

.filters__categories {
  position: relative;
}

.filters__btn {
  color: #010101;
  background-color: #0000;
  border: 1px solid #0101014d;
  border-radius: 30px;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 14px 20px;
  font-family: Neue Montreal;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.29;
  display: flex;
}

.filters__icon {
  fill: none;
  stroke: #010101;
}

.filters__list {
  z-index: 1;
  background: #fafafa;
  border-radius: 15px;
  flex-direction: column;
  gap: 8px;
  width: 100%;
  padding: 14px 20px;
  display: flex;
  position: absolute;
  bottom: -314px;
  left: 0;
  box-shadow: 0 20px 69px #00000012, 0 4px 11.212px #0000000a;
}

.filters__item {
  color: #01010180;
  font-family: Neue Montreal;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.29;
}

.filters__item--checked {
  color: #010101;
}

.filters__alphabet {
  position: relative;
}

.filters__alphabet .filters__btn {
  background-color: #fafafa;
  border: none;
}

.filters__alphabet .filters__list {
  bottom: -210px;
}

.popular__title {
  color: #010101;
  margin-bottom: 20px;
  font-family: Neue Montreal;
  font-size: 24px;
  font-weight: 500;
  line-height: 1.17;
}

.popular__list {
  flex-direction: column;
  gap: 14px;
  display: flex;
}

.popular__item {
  background: #fafafa;
  border-radius: 10px;
  gap: 10px;
  padding: 12px;
  display: flex;
  position: relative;
}

.popular__wrapper {
  background-color: #e8e8e2;
  border-radius: 10px;
  width: 74px;
  height: 74px;
}

.popular__subtitle {
  color: #010101;
  margin-bottom: 12px;
  font-family: Neue Montreal;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.11;
}

.popular__points {
  flex-wrap: wrap;
  gap: 9px;
  width: 152px;
  display: flex;
}

.popular__point {
  color: #0101014d;
  font-family: Neue Montreal;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.17;
}

.popular__span {
  color: #010101;
}

.popular__cart {
  background-color: #e8e8e2;
  border-radius: 50%;
  padding: 4px;
  line-height: 0;
  position: absolute;
  top: 12px;
  right: 12px;
}

.popular__icon {
  fill: #6d8434;
}
